import React from 'react';
import { Link } from 'react-router-dom';
import Nav from '../../Components/Nav';
import Content from '../../Components/Content';
import GithubIcon from '../../static/github.svg';
import ArrowIcon from '../../static/proximo.svg';
import {Image} from 'cloudinary-react';
import CV from '../../static/CV.pdf';

import './style.scss';

export default function (){
    console.log(CV);
    return(
        <div id="home">
        <Nav
            active="HOME"
        />
        <Content
            className="flex-column"
        >
          
            <div>
                <a
                    aria-label="página de github"
                    href="https://github.com/JoaquinNoguera"
                    className="social-icon"
                >
                    <GithubIcon/>
                </a>
            </div>
            
            <div 
                className="cover"
            >
                <div
                    className="arrow"
                >
                    <Link 
                        to="/porfolio"
                        aria-label="sección porfolio"
                    >
                        <ArrowIcon/>
                    </Link>
                </div>

                <Image 
                    cloudName="dbtp7mkgk" 
                    publicId="porfolio/DSCN5169_vnq1bv.jpg" 
                    width="320" 
                    height="352"  
                    crop="scale"
                    alt="Lugar de trabajo"
                />
            </div>
            <h1>
                Joaquín Noguera Velazquez
            </h1>
            
            <h2> 
                Desarrollador Web
            </h2>
            <a 
                aria-label="curriculum"
                href={CV}
            >
            <button>
                DESCARGAR CV
            </button>
            </a>
        </Content>
        </div>
    );
}